import { setAlias } from "@core/util/meta/alias";

export enum MemberTypeEnum {
    SUPPLIER = 1,
    AGENT,
    CONSUMER,
}
setAlias(MemberTypeEnum, [
    { value: MemberTypeEnum[MemberTypeEnum.SUPPLIER], alias: "" },
    { value: MemberTypeEnum[MemberTypeEnum.AGENT], alias: "" },
    { value: MemberTypeEnum[MemberTypeEnum.CONSUMER], alias: "" },
])