import { EntityEnum } from "../../../entity.enum";
import { checkDev, checkAdmin } from "../util.service";
import { C, U, S, AC1 } from "@core/util/meta/Power";
import { CustomUrl } from "@core/util/meta/CustomUrl";
import { MetaEntity } from "@core/util/meta/MetaEntity";
import { Member } from "../entity/Member";
import { Table } from "@core/util/struct/Table";
import { MemberTypeEnum } from "../enum/MemberType.enum";
import { Prop } from "@core/util/meta/Prop";
import { Check } from "@core/util/meta/Check";
import { Rules } from "@core/util/regexp";
import { Select } from "@core/util/meta/types/Select";
import { getAlias } from "@core/util/meta/alias";
import { AuditStatusEnum } from "../enum/AuditStatus.enum";
import { MetaFlow } from "@core/util/meta/Flow";
import { Toolbar } from "@core/util/meta/Toolbar";
import { BaseAduitEntity } from "../entity/BaseAduitEntity";
import { Product } from "../entity/Product";
import { ID } from "@core/util/meta/ID";
import { State } from "@core/util/meta/State";
import { Picture } from "@core/util/meta/types/Picture";
import { ProductTypeEnum } from "../enum/ProcutType.enum";
import { SellTypeEnum } from "../enum/SellType.enum";
import { Flight } from "../entity/Flight";
import { Adapter } from "@core/util/meta/Adapter";
import { OneToOne } from "@core/util/meta/ref/OneToOne";
import { FlightManageSearch } from "./FlightManage";
import { DateArray } from "@core/util/meta/types/DateArray";
import { SelectOne } from "@core/util/meta/ref/SelectOne";
import { ProductTargetEnum } from "../enum/ProductTarget";
import { OneToMany } from "@core/util/meta/ref/OneToMany";

export function canEdit() {
    if (checkDev() || checkAdmin()) {
        return C | U | S
    } else {
        return S;
    }
}
export let SellerAdapter = {
    read: (items: MemberSallerrManage[]) => Array.isArray(items) && items ? items.map(item => item.name) : [],
    write: (members: MemberSallerrManage[]) => members ? (Array.isArray(members) ? members.map(member => member.id).join(',') : members) : ''
}
@CustomUrl({ create: '/api/member/create' })
@MetaEntity({ objectName: "", objectCode: EntityEnum.Member, data: { presetObject: { memberType: MemberTypeEnum.AGENT } } })
export class MemberSallerrManage implements Table<Member> {
    id: number;
    /**+,:SBC8)*/
    @Prop("", { power: S })
    code: string;
    @Check({ maxLength: 7, minLength: 2 })
    @Prop("")
    name: string;
    @Check(Rules.regex.mobile)
    @Prop("")
    mobile: string;
    @Check({ minLength: 2, maxLength: 7 })
    @Prop("")
    principal: string;
    @Prop("")
    address: string;
    @Prop("")
    bankInfo: string;
    @Prop("")
    taxCode: string;
    @Prop("", { options: getAlias(MemberTypeEnum) })
    memberType: Select<MemberTypeEnum>;
    @Prop("", { options: getAlias(AuditStatusEnum) })
    memberStatus: Select<AuditStatusEnum>;
}

enum PMCFlow {
    Info = "Info",
    Price = "Price",
    Target = "Target",
    Buyers = "Buyers"
}
@MetaFlow({
    create: [
        { title: "1.", id: PMCFlow.Info, actions: [{ label: "", to: PMCFlow.Price }] },
        { title: "2.", id: PMCFlow.Price, actions: [{ label: "", to: PMCFlow.Info }, { label: "", to: PMCFlow.Target }] },
        {
            title: "3.", id: PMCFlow.Target, actions: [{ label: "", to: PMCFlow.Price }, {
                label: "", to: (product: Product) => {
                    debugger;
                    return product.target == ProductTargetEnum.SPEC ? PMCFlow.Buyers : C
                }
            }]
        },
        { title: "4. ", id: PMCFlow.Buyers, actions: [{ label: "", to: PMCFlow.Target }, { label: "", nzType: "primary", to: C }] },
    ]
})
@Toolbar([{ label: "", multi: false, checkPower: AC1, eventName: 'verify' }])
@MetaEntity({
    objectName: "",
    objectCode: EntityEnum.Product, data: { customUrl: { create: '/api/product/create' } }
})
export class ProductManage extends BaseAduitEntity implements Table<Product>{
    @ID()
    @Prop("id", { power: 0 })
    id: number;
    /**(++2+4 :WUHBKKCZ0001)*/
    @State({ create: PMCFlow.Info })
    @Prop("")
    code: string;

    @State({ create: PMCFlow.Info })
    @Prop("")
    name: string;
    // /***/
    @State({ create: PMCFlow.Info })
    @Prop("")
    image: Picture;
    @Check(false)
    @State({ create: PMCFlow.Info })
    @Prop("")
    isDomesticLine: boolean;
    @State({ create: PMCFlow.Info })
    @Prop("", { options: getAlias(ProductTypeEnum) })
    productType: Select<ProductTypeEnum>;
    @State({ create: PMCFlow.Info })
    @Prop('', { options: getAlias(SellTypeEnum) })
    sellType: Select<SellTypeEnum>;
    @Adapter({ read: (flight: Flight) => flight && typeof flight != 'string' ? flight.code : flight })
    @State({ create: PMCFlow.Info })
    @OneToOne({ searchKey: 'code' })
    @Prop("")
    boundFlight: FlightManageSearch;

    @Adapter({ read: (flight: Flight) => flight && typeof flight != 'string' ? flight.code : flight })
    @State({ create: PMCFlow.Info })
    @OneToOne({ searchKey: "code" })
    @Prop("")
    returnFlight: FlightManageSearch;
    @State({ create: PMCFlow.Info })
    @Adapter(Rules.adapter.dateArrTransform)
    @Prop("")
    boundDates: DateArray;
    @State({ create: PMCFlow.Info })
    @Prop("")
    tripDays: number;
    @State({ create: PMCFlow.Info })
    @Adapter(Rules.adapter.dateArrTransform)
    @Prop("")
    returnDates: DateArray;
    @State({ create: PMCFlow.Price })
    @Prop("")
    seatCount: number;
    @State({ create: PMCFlow.Price })
    @Prop("")
    restSeat: number;
    @State({ create: PMCFlow.Price })
    @Prop("")
    priceTax_a: number;
    @State({ create: PMCFlow.Price })
    @Prop('')
    priceTax_c: number;
    @State({ create: PMCFlow.Price })
    @Prop('_')
    priceSupply_a: number;
    @State({ create: PMCFlow.Price })
    @Adapter(Rules.adapter.rmb)
    @Prop('_')
    priceSupply_c: number;
    @State({ create: PMCFlow.Price })
    @Adapter(Rules.adapter.rmb)
    @Prop('_')
    priceSelf_a: number;
    @State({ create: PMCFlow.Price })
    @Prop('_')
    priceSelf_c: number;
    @State({ create: PMCFlow.Price })
    @Prop('_')
    price2B_a: number;
    @State({ create: PMCFlow.Price })
    @Prop('_')
    price2B_c: number;
    @State({ create: PMCFlow.Price })
    @Prop('C_')
    price2C_a: number;
    @State({ create: PMCFlow.Price })
    @Prop('C_')
    price2C_c: string;
    @State({ create: PMCFlow.Price })
    @Prop("")
    saleRuler: string;
    @State({ create: PMCFlow.Price })
    @Prop("()")
    teamRate: number;
    @State({ create: PMCFlow.Price })
    @Prop("()")
    tripRate: number;
    @State({ create: PMCFlow.Target })
    @SelectOne(getAlias(ProductTargetEnum))
    @Prop('')
    target: ProductTargetEnum;
    @Check(false)
    @State({ create: PMCFlow.Buyers })
    @Adapter(SellerAdapter)
    @OneToMany(MemberSallerrManage)
    @Prop(",", { power: canEdit })
    buyers: string = '';
    @Prop('', { options: getAlias(AuditStatusEnum) })
    auditStatus: Select<AuditStatusEnum>;
    @Prop("")
    changeLog: string;
}

