import { MetaNotify } from "@core/util/meta/IMetaNotify";
import { MsgNotify } from "app/projects/hk/entity/MsgNotify";
import { EntityEnum } from "app/entity.enum";
import { MsgTypeEnum } from "app/projects/hk/enum/MsgType.enum";
import { ModeEnum } from "@core/util/meta/Mode.enum";
import { Member } from "app/projects/hk/entity/Member";
import { IMsgNotify } from "@core/util/notify/IMsgNotify";
import { MetaEntity } from "@core/util/meta/MetaEntity";
import { ID } from "@core/util/meta/ID";
import { Prop } from "@core/util/meta/Prop";
import { SelectOne } from "@core/util/meta/ref/SelectOne";
import { getAlias } from "@core/util/meta/alias";
import { MemberTypeEnum } from "../enum/MemberType.enum";
import { User } from "app/blueprint/rbac/entity/User";
import { Table } from "@core/util/struct/Table";
import { Toolbar } from "@core/util/meta/Toolbar";
import { AuditStatusEnum } from "../enum/AuditStatus.enum";
import { I } from "@core/util/meta/Power";

@MetaNotify({ viewName: "member_register_notify", isWebpush: true, msgType: MsgTypeEnum.Notify })
@MetaEntity({ objectName: "", objectCode: EntityEnum.Member, defaultMode: ModeEnum.Info })
export class MemberRegisterNotify extends MsgNotify implements IMsgNotify, Table<Member>{
    @ID()
    @Prop("ID", { power: 0 })
    id: number;
    @Prop("", { power: I })
    title: string;
    @Prop("", { readonly: true })
    name: string;
    orgId: number = 1;
    @SelectOne(getAlias(MemberTypeEnum))
    @Prop("", { readonly: true })
    memberType: MemberTypeEnum;
    user: User
    getTitle = () => `` + this.name + `,`;
    getAddtion = () => JSON.stringify({ id: this.id });
    getContent = () => ` ${this.name},`;
    getUserId = () => this.user.id;
    getOrgId = () => null;
}

@MetaNotify({ viewName: "verify_member_task", msgType: MsgTypeEnum.Task, isWebpush: true })
@Toolbar([{ label: "", eventName: "veirfy_member_success", multi: false, url: '/app/member/verify/pass' }])
@MetaEntity({ objectName: "", objectCode: EntityEnum.Member })
export class MemberRegisterTask extends MsgNotify implements IMsgNotify, Table<Member>{
    id: number;
    @Prop("", { readonly: true })
    name: string;
    isJPush = false;
    @Prop("", { readonly: true })
    memberType: MemberTypeEnum;
    @SelectOne(getAlias(AuditStatusEnum))
    @Prop("")
    memberStatus: AuditStatusEnum;
    userId: number;
    user: User
    getTitle = () => ` ` + this.name + `,`;
    getAddtion = () => JSON.stringify({ id: this.id, extral: "" });
    getContent = () => ` ${this.name},`;
    getMsgType = () => MsgTypeEnum.Task;
    getUserId = () => JSON.parse(localStorage.getItem("employee")).id;
    getOrgId = () => JSON.parse(localStorage.getItem("employee")).orgId;
} 